.header-content,
.app {
padding-left: .8em;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  backdrop-filter: blur(20px) brightness(1.1);
  font-family: 'Source Serif Pro', sans-serif;
  font-weight: bold;
}

[data-theme="dark"] header {
  backdrop-filter: blur(30px) brightness(.7);
}

.header-content {
  width: clamp(385px, 90%, 50em);
  padding-block: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: .5em;
}

.header-logo {
  font-size: 1.3rem;
}

.theme-toggle {
  cursor: pointer;
  padding-block: .2em;
  padding-inline: .3em;
  border: none;
  border-radius: .3em;
  /* background-color: var(--bg-color-dark); */
  font-size: .9rem;
  font-family: var(--ff-sans);
  color: var(--color-dark);
  opacity: 0.7;
  transition: opacity 100ms ease;
}

.theme-toggle:hover {
  opacity: 1;
}

/* [data-theme="dark"] .theme-toggle {
  color: var(--color);
} */

.app {
  /* width: clamp(20em, 50%, 40em); */
  height: 100%;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 1 auto;
  margin-top: 5em;
  display: flex;
  flex-direction: column;
  gap: .5em;
}

.cards-masonry {
  margin: 0;
  padding: 0;
  width: min-content;
  display: flex;
  gap: 1em;
  opacity: 0;
  transition: opacity 500ms ease;
}

.cards-column {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.card-title {
  margin-block: .5em;
}

.card-links {
  display: flex;
  gap: .5em;
}

footer {
  margin-top: 3em;
  margin-bottom: 1em;
  flex-shrink: 0;
  padding-block: .5em;
  display: flex;
  justify-content: center;
  font-size: .9rem;
  font-family: var(--ff-serif);
}