@import url('http://fonts.cdnfonts.com/css/source-serif-pro');
@import url('http://fonts.cdnfonts.com/css/source-sans-pro');

:root {
  --bg-color: white;
  --bg-color-dark: #030303;
  --bg-color-primary: #3366ee;
  
  --color: black;
  --link-color: #3366ee;
  --color-dark: white;
  --link-color-dark: #80a0f6;

  --ff-serif: 'Source Serif Pro', serif;
  --ff-sans: 'Source Sans Pro', serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  color: inherit;
}

html {
  background-color: var(--bg-color);
  font-size: 20px;
  font-family: var(--ff-sans);
}

html[data-theme="dark"] {
  background-color: var(--bg-color-dark);
  color: var(--color-dark);
}

h1,
h2,
h3 {
  font-family: 'Source Serif Pro', sans-serif;
}

.pill {
  border-radius: 20%/50%;
}

.fade-in {
  opacity: 1;
}

.button {
  display: inline-block;
  text-decoration: none;
  padding-block: .2em;
  padding-inline: .5em;
  border: 1px solid gray;
  border-radius: .3em;
  color: inherit;
}

.button.primary {
  border: none;
  background-color: var(--bg-color-primary);
  font-weight: bold;
  color: var(--color-dark);
}

.link {
  text-decoration: none;
  font-weight: 600;
  color: var(--bg-color-primary);
}

[data-theme="dark"] .link {
  color: var(--link-color-dark);
}

.link:hover {
  text-decoration: underline;
}